import { Helmet } from 'react-helmet-async';
import { useNavigate, useParams } from 'react-router-dom';
import { Link as RouterLink } from 'react-router-dom';
import { Tooltip, Grid, Container, Typography, Button, CircularProgress } from '@mui/material';
import ArrowBackIcon from '@mui/icons-material/ArrowBack';

import PageTitleWrapper from 'src/components/PageTitleWrapper';
import Footer from 'src/components/Footer';
import { useGetInventoryByIdQuery } from 'src/services/baseApi';
import Content from './Content';

function InventoryReport() {

  let { id } = useParams();
  const inventoryId = id ? parseInt(id) : 0;

  const { data, isLoading, isSuccess } = useGetInventoryByIdQuery(
    { inventoryId },
    { refetchOnMountOrArgChange: true, refetchOnFocus: true, refetchOnReconnect: true }
  );

  if (isLoading) return (
    <CircularProgress />
  );
  
  return (
    <>
      <Helmet>
        <title>Cálculo do Inventário</title>
      </Helmet>
      <PageTitleWrapper>
        <Grid container justifyContent="space-between" alignItems="center">
          <Grid item>
            <Typography variant="h3" component="h3" gutterBottom>
              {`Cálculo do Inventário de ${data.decujus.nome}`}
            </Typography>
            <Typography variant="subtitle2">
              Visualize aqui o cálculo do inventário
            </Typography>
          </Grid>
          <Grid item>
            <Tooltip arrow title="Voltar para lista de inventários">
              <Button
                sx={{ mt: { xs: 2, md: 0 } }}
                variant="outlined"
                startIcon={<ArrowBackIcon fontSize="small" />}
                component={RouterLink}
                to="/inventories/list"
              >
                Voltar
              </Button>
            </Tooltip>
          </Grid>
        </Grid>
      </PageTitleWrapper>
      <Container maxWidth="lg">
        <Grid
          container
          direction="row"
          justifyContent="center"
          alignItems="stretch"
          spacing={3}
        >
          <Grid item xs={12}>
            {/* <Content
              data={data}
            /> */}
            <Typography variant="h4" component="h4" gutterBottom>
              Em Breve
            </Typography>
          </Grid>
        </Grid>
      </Container>
      <Footer />
    </>
  );
}

export default InventoryReport;
